@use "../../../../themes/prc_parent/src/scss/master.variables.scss" as variables;
@use "../../../../themes/prc_parent/src/scss/colors.scss" as colors;
@use "../../../../themes/prc_parent/src/scss/functions.scss" as functions;
@use "../../../../themes/prc_parent/src/scss/mixins.scss" as mixins;
@use "sass:math";

$relaxedBorder: #e6e7e6;
$altCardHeaderPadding: 1em;
$altCardLineHeight: 1.28571429em;
$altCardMargin: 1rem;
$breakpoint: 767px;

.column > .wp-block-group.is-style-card-alt,
#secondary.column > .prc-block-area {
	@media only screen and (min-width: $breakpoint) {
		margin-left: -1.5rem;
	}
}

.wp-block-group {
	& > :last-child {
		margin-bottom: 0;
	}

	.block-editor-block-list__layout > .wp-block {
		max-width: 1152px !important
	}

	&.has-background {
		padding: 1em;
		margin-bottom: 2em;
	}

	&.is-style-200-wide {
		width: 100%;
		max-width: 200px!important;
	}

	&.is-style-300-wide {
		width: 100%;
		max-width: 300px!important;
	}

	&.is-style-420-wide {
		width: 100%;
		max-width: 420px!important;
	}

	&.is-style-640-wide {
		width: 100%;
		max-width: 640px!important;
	}

	&.is-style-callout {
		border-top: 1px solid $relaxedBorder;

		p {
			font-family: franklin-gothic-urw, Verdana, Geneva, sans-serif;
			font-size: 14px;
		}

		.wp-block-group__inner-container ul li {
			font-family: sans-serif !important;
			font-size: 13px;
		}
	}

	&.is-style-card-alt {
		@media only screen and (min-width: $breakpoint) {
			padding-left: $altCardMargin;
			padding-right: $altCardMargin;
		}

		padding-bottom: $altCardMargin;

		.is-style-sub-header {
			background-color: #333132;
			padding: 9px $altCardHeaderPadding;
			margin-left: -$altCardMargin;
			margin-right: -$altCardMargin;
			margin-bottom: $altCardMargin;
			line-height: $altCardLineHeight;
			color: white;
			font-weight: normal;
			letter-spacing: 0.1em;

			a {
				color: white;
			}
		}
	}

	&.is-style-essay-toolbar {

		.meta,
		.icon {
			color: black !important;
		}

		.meta {
			font-size: 14px;
			letter-spacing: inherit;
		}

		.icon {
			font-size: 16px;
		}
	}

	&.relaxed {
		.ui.button {
			display: block;
			margin-left: auto;
			margin-right: auto;
		}
	}

	// Ensures grid blocks take up the full width of a group.
	div[data-type="prc-block/columns"] {
		max-width: 100% !important;
	}

	&.alignright {
		@media only screen and (min-width: variables.$largestTabletScreen) {
			/*rtl:ignore*/
			clear: right;
			float: right;
			margin-left: 1em;
			/*rtl:ignore*/
		}
	}
	&.alignleft {
		@media only screen and (min-width: variables.$largestTabletScreen) {
			/*rtl:ignore*/
			clear: left;
			float: left;
			margin-right: 1em;
			/*rtl:ignore*/
		}
	}
	&.aligncenter {
		&.is-style-640-wide,
		&.is-style-420-wide,
		&.is-style-300-wide,
		&.is-style-200-wide {
			margin-left: auto!important;
			margin-right: auto!important;
		}
	}
}

.wp-block-group.essay-toolbar-group {

	.meta,
	.icon {
		color: black !important;
	}

	.meta {
		font-size: 14px;
		letter-spacing: inherit;
	}

	.icon {
		font-size: 16px;
	}
}
